<template>
  <div class="incomeDetails">
	  <div class="incomeDetails_head">
		<i class="icon_arrow_left" @click="goBack"></i>
		<p class="incomeDetails_title">积分明细</p>
	  </div>
	 <!-- <div class="incomeDetails_type">
		  <p :class="nowType == item.id?'act':''" v-for="item in typeList" :key="item.id" @click="selectType(item)">{{ item.text }}</p>
	  </div> -->
	  <p class="yearMonth" @click="showPicker"><span>{{ YearMonth }}</span><i class="icon_arrow_bottom"></i></p>
	  <ul class="incomeList">
	  	<li v-for="(item,index) in incomeList" :key="item.id">
			<img :src="item.transactionState == 1? redbag01 : redbag02" alt="">
			<div class="incomeList_box">
				<p class="incomeList_box_title">{{ nowType == 0 ?item.modularType : item.orderStatus == 1? '提现成功':'审核中' }}</p>
				<p class="incomeList_box_time">{{ new Date(item.createTime).toLocaleString('chinese',{hour12:false}).replace(/\//g,'-') }}</p>
			</div>
			<div class="incomeList_amount">
				<p class="incomeList_amount_watch" v-show="nowType == 1" @click="showDetails(item)">查看详情</p>
				<p class="incomeList_amount_ss">
					<i></i>
					<span>{{ item.transactionState == 1 ? '+'+item.amount:'-'+item.amount }}</span>
				</p>
			</div>
		</li>
	  </ul>
	  <div v-show="incomeList.length == 0">
		  <van-empty description="暂无记录" />
	  </div>
	  <div class="shadow" v-show="shadowFlag">
		  <div class="picker_box">
			<van-datetime-picker
			v-model="currentDate"
			type="year-month"
			title="选择年月"
			:min-date="minDate"
			:max-date="maxDate"
				@confirm="confirm"
				@cancel="cancel"
			:formatter="formatter"
			/>
		  </div>
	  </div>
	  <div v-show="loadingFlag" class="shadow">
	  	<van-loading size="36px" vertical text-color="#ffffff" color="#ffffff">加载中...</van-loading>
	  </div>
  </div>
</template>

<script>
  export default {
    name:'incomeDetails',
    data(){
      return {
		redbag01:require('../static/img/icon_redbag01.png'),
		redbag02:require('../static/img/icon_redbag02.png'),
		obj:{},
		nowType:0,
		typeList:[
			{
				id:0,
				text:'福利值明细'
			},
			{
				id:1,
				text:'提现明细'
			}
		],
		shadowFlag:false,
		YearMonth:new Date().getFullYear()+'-'+(new Date().getMonth()+1 < 10 ? '0'+(new Date().getMonth()+1) : new Date().getMonth()+1),
		YearMonthDate:new Date(),
        minDate: new Date(2020, 0, 1),
		maxDate: new Date(),
		currentDate: new Date(),
		incomeList:[],
		loadingFlag:false
      }
    },
	mounted(){
		var jsonObj = localStorage.getItem("obj");
		this.obj = JSON.parse(jsonObj);
		this.accountList();
		// this.withdrawalList();
	},
	methods:{
		goBack(){
		  this.$router.go(-1);
		},
		formatter(type, val) {
		  if (type === 'year') {
			return `${val}年`;
		  } else if (type === 'month') {
			return `${val}月`;
		  }
		  return val;
		},
		confirm(e){
			let _this = this;
			var YearMonth = e.getFullYear()+'-'+(e.getMonth()+1 < 10 ? '0'+(e.getMonth()+1) : e.getMonth()+1);
			_this.YearMonth = YearMonth;
			_this.YearMonthDate = e;
			if(_this.nowType == 0){
				_this.accountList();
			}else if(_this.nowType == 1){
				_this.withdrawalList();
			}
			this.shadowFlag = false;
		},
		cancel(e){
			this.shadowFlag = false;
		},
		showPicker(){
			this.shadowFlag = true;
		},
		selectType(data){
			this.nowType = data.id;
			if(data.id == 0){
				this.accountList();
			}else if(data.id == 1){
				this.withdrawalList();
			}
		},
		showDetails(data){
			console.log('跳转详情页');
			let _this = this;
			this.$router.push({path:'/withdrawalDetails',query:{item:data}});
		},
		accountList(){//获取福利值列表
			let _this = this;
			_this.loadingFlag = true;
			var exclusiveObj = {
				createTime:_this.YearMonthDate,
				pageSize:'100',
				pageNum:'0'
			};
			var requestData = Object.assign(_this.obj,exclusiveObj);
			console.log(requestData);
			var redata = _this.byte256.byte256.encode(_this.key, JSON.stringify(requestData));
			_this.$axios({
			  method: 'post',
			  url: '/integralRecord',
			  data:redata
			}).then(function(res) {
			  console.log('accountList=========',res);
			  if(res.data.code === 200){
				var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
				console.log(reData);
				_this.incomeList = reData;
			  }else{
				_this.$toast(res.data.msg);
				_this.incomeList = [];
			  }
			  _this.loadingFlag = false;
			});
		},
		withdrawalList(){
			let _this = this;
			_this.loadingFlag = true;
			var ss = 'dateTime='+_this.YearMonth+'&pageSize=100&mediaCode='+ _this.obj.mediaCode +'&pageNum=1&userId='+_this.obj.userId;
			var data = _this.byte256.byte256.encode(_this.key,ss);
			_this.$axios({
			  method: 'get',
			  url: '/withdrawalList?params='+encodeURIComponent(data),
			}).then(function(res) {
			  console.log('withdrawalList=========',res);
			  if(res.data.code === 200){
				var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
				console.log(reData);
				_this.incomeList = reData.rows;
			  }else{
				_this.$toast(res.data.msg);
				_this.incomeList = [];
			  }
			  _this.loadingFlag = false;
			});
		}
	}
  }
</script>

<style scoped>
	.incomeDetails{
		width: 10.8rem;
		min-height: 100vh;
		background-color: #fcebfe;
	}
	.incomeDetails_head{
		display: flex;
		align-items:center;
		width: 10.8rem;
		height: 2.32rem;
		background: linear-gradient(to left,#fe7773,#fb4377,#fa8dea);
		box-sizing: border-box;
		padding-top:var(--barHeight);
		padding-left:0.3rem;
		padding-right:0.3rem;
		position: relative;
	}
	.icon_arrow_left{
	  display: inline-block;
	  width: 0.56rem;
	  height:0.56rem;
	  background-image: url(../static/img/icon_arrow_left.png);
	  background-repeat: no-repeat;
	  background-size: 100%;
	  z-index: 4;
	}
	.incomeDetails_title{
		width: 100%;
		height: 1.52rem;
		line-height: 1.52rem;
		position: absolute;
		left:0;
		bottom:0;
		font-size: 0.48rem;
		color:#fff;
		text-align: center;
		z-index: 3;
	}
	.incomeDetails_type{
		width:10.8rem;
		height: 1.3rem;
		overflow: hidden;
	}
	.incomeDetails_type p{
		width:50%;
		height:1.3rem;
		text-align: center;
		color:#747474;
		line-height: 1.3rem;
		font-size: 0.48rem;
		background-color: #e9e9e9;
		float: left;
	}
	.incomeDetails_type p.act{
		background-color: #ff9000;
		color:#fff;
		position: relative;
	}
	.incomeDetails_type p.act::after{
		content: "";
		width: 0;
		height:0;
		border-left: 0.2rem solid transparent;
		border-right: 0.2rem solid transparent;
		border-bottom: 0.4rem solid #f2f2f2;
		position: absolute;
		bottom: -0.2rem;
		left:50%;
		transform: translateX(-50%);
	}
	.picker_box{
		width: 10.8rem;
		position: fixed;
		left:0;
		bottom:0;
		z-index: 100;
	}
	.yearMonth{
		font-size: 0.48rem;
		color: #000;
		line-height: 1.64rem;
		width: 10.8rem;
		height: 1.64rem;
		box-sizing: border-box;
		padding:0 0.3rem;
	}
	.yearMonth span{
		display: inline-block;
		vertical-align: middle;
	}
	.icon_arrow_bottom{
		display: inline-block;
		vertical-align: middle;
		width: 0.26rem;
		height:0.16rem;
		background-image: url(../static/img/icon_arrow_bottom.png);
		background-repeat: no-repeat;
		background-size: 100%;
		margin-left:0.3rem;
	}
	.incomeList{
		width: 10.8rem;
		box-sizing: border-box;
		padding: 0 0.3rem 0.5rem 0.3rem;
	}
	.incomeList li{
		display: flex;
		align-items:center;
		justify-content: space-between;
		width: 100%;
		height: 1.84rem;
		border-radius: 0.15rem;
		background-color: #fff;
		box-sizing: border-box;
		padding:0 0.3rem;
		margin-bottom:0.3rem;
	}
	.incomeList li img{
		width:1.2rem;
		height:1.2rem;
	}
	.incomeList_box{
		width: 5.8rem;
		font-weight: 600;
	}
	.incomeList_box_title{
		font-size: 0.48rem;
		color:#000;
		line-height: 0.8rem;
	}
	.incomeList_box_time{
		font-size: 0.36rem;
		color:#afb0b4;
		line-height: 0.72rem;
	}
	.incomeList_amount{
		width: 1.7rem;
		text-align: right;
	}
	.incomeList_amount_watch{
		font-size: 0.36rem;
		color:#0084ff;
		line-height: 0.8rem;
		
	}
	.incomeList_amount_ss i{
		display: inline-block;
		vertical-align: middle;
		width: 0.5rem;
		height:0.5rem;
		background-image: url(../static/img/icon_gold.png);
		background-repeat: no-repeat;
		background-size: 100%;
	}
	.incomeList_amount span{
		display: inline-block;
		vertical-align: middle;
		font-size: 0.48rem;
		color:#ff0000;
		font-weight: 600;
	}
</style>
